import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import Section from "../components/Section";
import SectionHeader from "../components/SectionHeader";
import { Link } from "react-router-dom";
import Card from "../components/Card";
import sparesData from "../utils/sparesData";
import Breadcrumbs from "../UI/Breadcrumbs";
import Brands from "../call-to-action/Brands";
import { Helmet } from "react-helmet";

const CataloguePage = () => {
  const { id } = useParams();
  const [category, setCategory] = useState();
  const [items, setItems] = useState([]);

  const captionStyle = "p-2";

  console.log(id);

  useEffect(() => {
    setCategory(id);

    setItems(
      sparesData.filter((item) => {
        return item.category === category;
      })
    );
  }, [category]);

  return (
    <div className="mb-10">
      {category && (
        <div>
          {category === "blade" && (
            <Helmet>
              <title> -  </title>
              <meta
                name="description"
                content="        -10  -20.        -   ,    ,  ,     .               .          !"
              />
            </Helmet>
          )}

          {category === "spare" && (
            <Helmet>
              <title> - </title>
              <meta
                name="description"
                content="         .            -90   -75.         ,      .        .          !"
              />
            </Helmet>
          )}
          {category === "engine" && (
            <Helmet>
              <title> - </title>
              <meta
                name="description"
                content="           .     ,   -41  -238.          .          !"
              />
            </Helmet>
          )}
        </div>
      )}

      <Section>
        <Breadcrumbs />
        <SectionHeader
          headerText=" "
          subheaderText="         "
          styles="mt-6 lg:mt-8"
        />
        <div>
          <ul className="grid grid-cols-2 grid-rows-[repeat(4,130px)] gap-4 md:gap-6 md:grid-cols-4 md:grid-rows-[230px]">
            {items.map((item, index) => {
              if (item.position === "span-2") {
                return (
                  <li key={index} className="row-span-2 h-auto">
                    <Link to={`/eqipment/${item.category}/${item.url}`}>
                      <Card
                        img={item.img}
                        alt={item.alt}
                        caption={item.name}
                        captionStyle={captionStyle}
                        style=""
                      />
                    </Link>
                  </li>
                );
              } else {
                return (
                  <li key={index} className="">
                    <Link to={`/eqipment/${item.category}/${item.url}`}>
                      <Card
                        img={item.img}
                        alt={item.alt}
                        caption={item.name}
                        captionStyle={captionStyle}
                      />
                    </Link>
                  </li>
                );
              }
            })}
          </ul>
        </div>
      </Section>
      <div className="bg-color_light_gray">
        <Brands />
      </div>
    </div>
  );
};

export default CataloguePage;
